export const getNumber = (value: any) => {
  const unformattedValue: number = currencyFormatToNumber(value);
  return unformattedValue;
};

export const getNumberIncludeNull = (value: any) => {
  const unformattedValue: number | null = currencyFormatToNumberIncludeNull(value);
  return unformattedValue;
};

export const getString = (value: string | number) => {
  if (!value) {
    return "";
  }
  return value?.toString();
};

export const getDecimalValueForCurrency = (value: any) => {
  const output = parseFloat(value);

  if (!isNaN(output)) {
    return output?.toString()?.slice(0, 2);
  } else {
    return "";
  }
};
export const showCurrency = (value: string | number) => {
  let decimalValue = "";
  const inputInString = value?.toString();
  // let inputForNumberFormatter =inputInString
  let inputForNumberFormatter = getNumber(value);
  // let decimalValue ="";
  if (inputInString?.includes(".") && inputInString) {
    const numSplitByDot = inputInString?.split(".");
    inputForNumberFormatter = getNumber(numSplitByDot[0]);
    if (numSplitByDot[1] || numSplitByDot[1] === "") {
      decimalValue = "." + getDecimalValueForCurrency(numSplitByDot[1]);
    }
  }
  const numberFormatterOutput = numberFormatter(inputForNumberFormatter, "en-IN", 0);
  return numberFormatterOutput?.toString() + decimalValue;
};

export const showCurrencyIncludeNull = (value: string | number | null) => {
  let decimalValue = "";
  // const num = getNumberIncludeNull(value);
  const inputInString = value?.toString();
  let inputForNumberFormatter = getNumberIncludeNull(value);
  if (inputForNumberFormatter === null) {
    return null;
  }
  if (inputInString?.includes(".") && inputInString) {
    const numSplitByDot = inputInString?.split(".");
    inputForNumberFormatter = getNumberIncludeNull(numSplitByDot[0]);
    if (numSplitByDot[1] || numSplitByDot[1] === "") {
      decimalValue = "." + getDecimalValueForCurrency(numSplitByDot[1]);
    }
  }
  if (inputForNumberFormatter == null) {
    return "" + decimalValue;
  }
  return numberFormatterIncludeNull(inputForNumberFormatter, "en-IN", 0) + decimalValue;
};

