export const updateCamValuationListObj = (stageData: any, data: any): ICamPropertyValuationData => {
  const collateralProperties: ICamCollateralPropertyData[] = [];

  data?.forEach((property: any) => {
    const valuationTable: ICamValuationRowData[] = [];
    property?.stages?.forEach((valuation: any) => {
      const venderValuation: ICamValuationRowData = {
        uid: valuation?.uid ?? "",
        partner: {
          name: valuation?.partner?.name ?? "",
        },
        stageCompletionDate: valuation?.stageCompletionDate ?? "",
        technical: {
          govtTotalValuationOfProperty: valuation?.technical?.fairMarketValue ?? "",
        },
      };
      valuationTable.push(venderValuation);
    });

    const collateralProperty: ICamCollateralPropertyData = {
      uid: property?.uid,
      address: property?.address,
      city: {
        name: property?.city?.name,
        state: {
          name: property?.city?.state?.name,
        },
      },
      pincode: {
        pinCode: property?.pincode?.pinCode,
      },
      pageMode: PAGE_MODE?.VIEW,
      propertyValuation: {
        smValuationOperationType: property?.propertyValuation?.smValuationOperationType,
        finalValuation: property?.propertyValuation?.finalValuation,
      },
      isPropertyValuatedByMethodOperation: !!property?.propertyValuation,
      valuationTable: valuationTable,
    };

    collateralProperties.push(collateralProperty);
  });

  const obj: ICamPropertyValuationData = {
    totalPropertyValuation: stageData?.totalPropertyValuation ?? "",
    collateralProperties: collateralProperties,
  };
  return obj;
};

