export const updateCamBankingSummaryPageObj = (data: any): ICamBankingSummaryRowData[] => {
  const bankingSummaryList: ICamBankingSummaryRowData[] = [];

  data?.forEach((record: any) => {
    const bankingSummary: ICamBankingSummaryRowData = {
      applicantType: record?.bankingDetail?.applicant?.applicantType ?? "",
      accountHolderName: record?.bankingDetail?.accountHolderName ?? "",
      bankName: record?.ifsc?.bank?.name ?? "", // field is not available yet  in api
      accountType: record?.bankingDetail?.smAccountType ?? "",
      accountNumber: record?.bankingDetail?.accountNo ?? "",
      odOrCcLimit: record?.bankingDetail?.odCcLimit ?? "",
      twelveMonthAbb: record?.bankingDetail?.twelveMonthAbbAvg ?? "",
      sixMonthAbb: record?.bankingDetail?.sixMonthAbbAvg ?? "",
      threeMonthAbb: record?.bankingDetail?.threeMonthAbbAvg ?? "",
      twelveMonthCreditAvg: record?.bankingDetail?.twelveMonthCreditAvg ?? "",
      currentSixMonthCredit: record?.bankingDetail?.current6MonthCreditAvg ?? "",
      previousSixMonthCredit: record?.bankingDetail?.previous6MonthCreditAvg ?? "",
      creditRate: record?.bankingDetail?.creditRate ?? "",
      inwardCheckReturn: record?.bankingDetail?.inwardChequeReturnPct ?? "",
      outwardCheckReturn: record?.bankingDetail?.outwardChequeReturnPct ?? "",
    };
    bankingSummaryList.push(bankingSummary);
  });

  return bankingSummaryList;
};

