export const updateCamMonthlyBankingAnalysisData = (
  data: any
): ICamMonthlyBankingAnalysisData[] => {
  const monthlyAnalysisByDay: ICamMonthlyBankingAnalysisData[] = [];

  data?.forEach((record: any) => {
    const columns: ICamMonthlyBankingAnalysisRowData[] = [];
    record?.bankingAnalysis?.forEach((column: any) => {
      const columnData: ICamMonthlyBankingAnalysisRowData = {
        uid: column?.uid,
        month: column?.month,
        year: column?.year,
        amount: column?.amount,
      };
      columns.push(columnData);
    });
    const analysis: ICamMonthlyBankingAnalysisData = {
      dayOfMonth: record?.dayOfMonth,
      bankingAnalysis: columns,
    };
    monthlyAnalysisByDay.push(analysis);
  });

  return monthlyAnalysisByDay;
};

export const updateCamCreditDebitTransactionTable = (
  data: any
): ICamCreditAndDebitTransactionRowData[] => {
  const creditDebitTransactionList: ICamCreditAndDebitTransactionRowData[] = [];

  data?.forEach((record: any) => {
    const creditDebitTransaction: ICamCreditAndDebitTransactionRowData = {
      uid: record?.uid,
      month: record?.month,
      year: record?.year,
      creditTransactionNo: record?.noOfCreditTransactions,
      debitTransactionNo: record?.noOfDebitTransactions,
      creditTransactionAmount: showCurrencyIncludeNull(record?.totalCreditAmount),
      debitTransactionAmount: showCurrencyIncludeNull(record?.totalDebitAmount),
      checkBounceInward: record?.noOfInwardChequeBounce,
      checkBounceOutward: record?.noOfOutwardChequeBounce,
    };
    creditDebitTransactionList.push(creditDebitTransaction);
  });

  return creditDebitTransactionList;
};

